#dark,
#dark header {
  background: #131722;
}
#dark header nav a {
  color: #ffffff;
}
#dark header nav a:hover {
  color: #007bff;
}
#dark header nav a.btn:hover {
  color: #ffffff;
}
#dark header nav .nav-item.active a {
  color: #007bff;
}
#dark .market-pairs {
  border: 1px solid #2a2e39;
  background: #131722;
}
#dark tr {
  background: #131722;
}
#dark .market-pairs .table td,
#dark .market-pairs .table th,
#dark .landing-feature {
  border-top: 1px solid #2a2e39;
}
#dark .market-history .table td,
#dark .market-history .table th {
  border-top: 1px solid #2a2e39;
}
#dark .order-book thead tr {
  border-bottom: 1px solid #2a2e39;
}
#dark .order-book,
#dark .market-history,
#dark .market-news,
#dark .landing-feature-item {
  border: 1px solid #2a2e39;
}
#dark tbody tr:hover {
  background: #2a2e39;
}
#dark .form-control::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #69727a;
}
#dark .form-control::-moz-placeholder {
  /* Firefox 19+ */
  color: #69727a;
}
#dark .form-control:-ms-input-placeholder {
  /* IE 10+ */
  color: #69727a;
}
#dark .form-control:-moz-placeholder {
  /* Firefox 18- */
  color: #69727a;
}
#dark tbody tr {
  color: #ffffff;
}
#dark thead th,
#dark .market-pairs .nav-link {
  color: #4f5966;
}
#dark .market-pairs .nav-link.active,
#dark #STAR i {
  color: #ffffff;
}
#dark .green {
  color: #26de81;
}
#dark .red {
  color: #ff231f;
}
#dark .market-pairs .nav {
  background: #1c2030;
}
#dark .market-pairs input,
#dark .market-pairs span {
  border-color: #2a2e39;
  background: #2a2e39;
}
#dark .market-pairs input {
  color: #ffffff;
}
#dark .heading {
  background: #1c2030;
  color: #ffffff;
}
#dark .ob-heading tr {
  border-top: 1px solid #2a2e39 !important;
  border-bottom: 1px solid #2a2e39 !important;
}
#dark .ob-heading tr:hover {
  background: #131722;
  cursor: default;
}
#dark .ob-heading span {
  color: #4f5966;
}
#dark .dropdown-menu {
  background: #131722;
  box-shadow: 0 0 10px 0 #131722;
  border: 1px solid #2a2e39;
}
#dark .dropdown-item:focus,
#dark .dropdown-item:hover {
  background-color: #2a2e39;
  color: #ffffff;
}
#dark .market-history .nav {
  background: #1c2030;
}
#dark .market-history .nav-link.active {
  color: #ffffff;
}
#dark .market-history .nav-link {
  color: #4f5966;
}
#dark .red-bg-80:after,
#dark .red-bg-60:after,
#dark .red-bg-40:after,
#dark .red-bg-20:after,
#dark .red-bg-10:after,
#dark .red-bg-8:after,
#dark .red-bg-5:after,
#dark .red-bg:after {
  background: #ff231f63;
}
#dark .market-news {
  background: #131722;
}
#dark .market-news li {
  border-bottom: 1px solid #2a2e39;
}
#dark .market-news li:hover {
  background: #2a2e39;
}
#dark .market-news li strong,
#dark .market-news li a {
  color: #c5cbce;
}
#dark .market-order {
  background: #131722;
}
#dark .no-data i,
#dark .no-data {
  color: #2a2e39;
}
#dark .market-trade {
  border: 1px solid #2a2e39;
  background: #131722;
}
#dark .market-trade .nav .nav-item .nav-link.active {
