  color: #ffffff;
}
#dark .market-trade .nav .nav-item .nav-link {
  color: #4f5966;
}
#dark .market-trade p {
  color: #c5cbce;
}
#dark .market-trade-list li a {
  background: #2a2e39;
  color: #ffffff;
}
#dark .market-trade input {
  border: 1px solid #2a2e39;
  background: #2a2e39;
  color: #ffffff;
}
#dark .market-trade .input-group-text {
  background: #2a2e39;
  color: #ffffff;
  border-color: #2a2e39;
}
